.TodoSearch {
    background: var(--seasalt);
    border-radius: 4px;
    border: 2px solid var(--dark-blue);
    height: 64px;
    margin-bottom: 10px;    
    width: calc(100% - 64px);
    max-width: 500px;
    font-size: 18px;
    text-align: center;
    font-weight: 400;
    color: #1E1E1F;
    box-shadow: 0px 5px 30px rgba(32, 35, 41, 0.25);
  }
  
.TodoSearch::placeholder {
  color: #A5A5A5;
  font-weight: 400;
}

.TodoSearch:focus {
  outline-color: var(--mid-blue);
}

.TodoSearch:disabled {
  opacity: 25%;
}